import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { BaseServiceService } from '@app/services/base-service.service';
import { NgbDateStruct } from '@ng-bootstrap/ng-bootstrap';
import axios from 'axios';

@Component({
  selector: 'app-order-entry-form',
  templateUrl: './order-entry-form.component.html',
  styleUrls: ['./order-entry-form.component.scss']
})
export class OrderEntryFormComponent implements OnInit {

  truckData:any = {};
  keyword = 'truck_number';
  truckList:any = [];
  
  constructor(private router: Router, private baseService: BaseServiceService,) { }

  ngOnInit(): void {
    this.getTrucksList();
  }

  resetForm(){
    this.truckData = {};
    return;
  }

 
  truckSelected(a) {
    console.log(a);
    this.getTruckById(a);
  }
  getTrucksList() {
    axios.get(this.baseService.baseUrl + 'api/truck',{
      
    })
      .then(response => {  
        console.log(response);
              
        this.truckList = response.data.data;
      })
      .catch(function (error) {
        console.log(error)
      })
  }
  getTruckById(obj) {
    axios.get(this.baseService.baseUrl + 'api/truck/' + obj.truck_number,{
      
    })
      .then(response => {        
       
        if (response.status) {
          this.truckData = response.data.data;
          console.log(this.truckData);
          console.log(response.data);
          
        }
      })
      .catch(function (error) {
        console.log(error)
      })
  }
  
  formSubmit() {
    axios.put(
      this.baseService.baseUrl + 'api/truck/'+  this.truckData._id,
      this.truckData,
      {
        // headers: {
        //   "Authorization": `Bearer ${this.authS.userValue.jwtToken}`
        // }
      }
    )
      .then(response => {
        console.log('resp: ', response);
        location.reload();
      })
      .catch(function (error) {
        console.log(error)
      })
  }



}
