import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { EntryFormComponent } from './entry-form/entry-form.component';
import { Routes, RouterModule } from '@angular/router';

import { SharedModule } from '@gaxon/modules';

import {NgxMasonryModule} from 'ngx-masonry';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { BrowserModule } from '@angular/platform-browser';
import { NgbDate,NgbModule } from '@ng-bootstrap/ng-bootstrap';
import { AutocompleteLibModule } from 'angular-ng-autocomplete';
import { TruckEntryFormComponent } from './entry-form/truck-entry-form/truck-entry-form.component';
import { OrderEntryFormComponent } from './entry-form/order-entry-form/order-entry-form.component';
const appsRoutes: Routes = [
  {
    path: 'entry-form',
  
    component: EntryFormComponent
  },
  {
    path: 'order-form',
  
    component: OrderEntryFormComponent
  },
  {
    path: '**',
    component: EntryFormComponent
  }
];

@NgModule({
  declarations: [EntryFormComponent, TruckEntryFormComponent, OrderEntryFormComponent],
  imports: [
    CommonModule,
    
    ReactiveFormsModule,NgbModule,
    AutocompleteLibModule,FormsModule,
    RouterModule.forChild(appsRoutes)
  ],exports:[EntryFormComponent, TruckEntryFormComponent, OrderEntryFormComponent]
})
export class MainBlockModule { }
