import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { NgbDateStruct } from '@ng-bootstrap/ng-bootstrap';
import { BaseServiceService } from '@app/services/base-service.service';
import Axios from 'axios'

@Component({
  selector: 'app-truck-entry-form',
  templateUrl: './truck-entry-form.component.html',
  styleUrls: ['./truck-entry-form.component.scss']
})
export class TruckEntryFormComponent implements OnInit {
  model: NgbDateStruct;
  truckData:any = {};
  keyword = 'truck_number';
  truckList:any = [];
  truckNumberSelected = '';
  
  constructor(private router: Router, private baseService: BaseServiceService,) { }

  ngOnInit(): void {
    this.getTrucksList();
  }

  truckSelected(a) {
    console.log(a);
    this.getTruckById(a);
    this.truckNumberSelected  = a.truck_number;
  }
  getTrucksList() {
    Axios.get(this.baseService.baseUrl + 'api/truck',{
      
    })
      .then(response => {  
        console.log(response);
              
        this.truckList = response.data.data;
      })
      .catch(function (error) {
        console.log(error)
      })
  }
  getTruckById(obj) {
    Axios.get(this.baseService.baseUrl + 'api/truck/' + obj.truck_number,{
      
    })
      .then(response => {        
       
        if (response.status) {
          this.truckData = response.data.data;
          console.log(this.truckData);
          console.log(response.data);
          
        }
      })
      .catch(function (error) {
        console.log(error)
      })
  }


  resetForm(){
    this.truckData = {};
  }

  formSubmit() {
    Axios.put(
      this.baseService.baseUrl + 'api/truck/'+  this.truckData._id,
      this.truckData,
      {
        // headers: {
        //   "Authorization": `Bearer ${this.authS.userValue.jwtToken}`
        // }
      }
    )
      .then(response => {
        console.log('resp: ', response);
        location.reload();
      })
      .catch(function (error) {
        console.log(error)
      })
  }










post() {
  let obj = {};
  Axios.post(this.baseService.baseUrl + '', obj,{
    // headers: {
    //   "Authorization": `Bearer ${this.authS.userValue.jwtToken}`
    // }
  })
    .then(response => {
      console.log('resp: ', response)

     
    })
    .catch(function (error) {
      console.log(error)
    })
}
  patch() {
    let obj = {};
    Axios.patch(
      this.baseService.baseUrl + '',
      obj,
      {
        // headers: {
        //   "Authorization": `Bearer ${this.authS.userValue.jwtToken}`
        // }
      }
    )
      .then(response => {
        console.log('resp: ', response)

       
      })
      .catch(function (error) {
        console.log(error)
      })
  }
}
